<template>
  <div class="default-margin">
    <v-card class="default-padding">
      <v-btn @click.native="$router.back()">返回</v-btn>
    </v-card>
    <v-card class="default-padding" style="margin-top: 12px;">
      <v-form :inline="true" class="demo-form-inline">
        <v-row>
          <v-col cols="12" sm="3" md="2">
            <v-btn @click="initIndex">
              初始化ES索引
            </v-btn>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-btn @click="importES">
              将数据从数据库导入ES
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
      <h4>从文件夹加载图片（本地操作）</h4>
      <v-form>
        <v-row>
          <v-col cols="12" sm="3" md="2">
            <v-text-field
              v-model="initForm.folderPath"
              placeholder="路径"
              clearable
              dense
              solo
              :hide-details="true"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="6" md="4">
            <select-user v-model="initForm.userId"></select-user>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-select
              v-model="initForm.privacy"
              :items="$enum.toArray($enum.PrivacyState)"
              item-text="label"
              item-value="value"
              placeholder="显示状态"
              dense
              solo
              :hide-details="true"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-btn @click="init">
              初始化
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
      <h4>采集失败标识不再采集</h4>
      <v-form>
        <v-row>
          <v-col cols="12" sm="3" md="2">
            <v-text-field
              v-model="giveUpErrorForm.status"
              placeholder="状态码"
              clearable
              dense
              solo
              :hide-details="true"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-btn @click="giveUpError">放弃标识</v-btn>
          </v-col>
        </v-row>
      </v-form>
      <h4>绑定用户（采集后处理）</h4>
      <v-form>
        <v-row>
          <v-col cols="12" sm="6" md="4">
            <SelectUser v-model="bindUserForm.userId"></SelectUser>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-btn @click="bindUser">绑定用户</v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import { mapActions } from "vuex"
import SelectUser from "./components/SelectUser"

export default {
  components: {
    SelectUser
  },
  data() {
    return {
      initForm: {
        folderPath: "",
        userId: "",
        privacy: this.$enum.getKey(
          this.$enum.PrivacyState,
          this.$enum.PrivacyState.PUBLIC
        )
      },
      bindUserForm: {
        userId: ""
      },
      giveUpErrorForm: {
        status: null
      }
    }
  },
  methods: {
    ...mapActions("picture", [
      "AInit",
      "ABindUser",
      "AInitIndex",
      "AImportES",
      "AGiveUpError"
    ]),
    async init() {
      const result = await this.AInit(this.initForm)
      if (result.status === 200) {
        this.$notify({ text: "成功", color: "success" })
      } else {
        this.$notify({ text: "失败", color: "error" })
      }
    },
    async bindUser() {
      const result = await this.ABindUser(this.bindUserForm)
      if (result.status === 200) {
        this.$notify({ text: "成功", color: "success" })
      } else {
        this.$notify({ text: "失败", color: "error" })
      }
    },
    async initIndex() {
      const result = await this.AInitIndex()
      if (result.status === 200) {
        this.$notify({ text: "成功", color: "success" })
      } else {
        this.$notify({ text: "失败", color: "error" })
      }
    },
    async importES() {
      const result = await this.AImportES()
      if (result.status === 200) {
        this.$notify({ text: "成功", color: "success" })
      } else {
        this.$notify({ text: "失败", color: "error" })
      }
    },
    async giveUpError() {
      const result = await this.AGiveUpError(this.giveUpErrorForm)
      if (result.status === 200) {
        this.$notify({ text: "成功", color: "success" })
      } else {
        this.$notify({ text: "失败", color: "error" })
      }
    }
  }
}
</script>

<style type="text/less" lang="less" scoped>
h4 {
  margin-top: 16px;
}
</style>
